
import { useState, useMemo } from "react";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Progress } from "@/components/ui/progress";
import { ChartContainer, ChartTooltip, ChartTooltipContent } from "@/components/ui/chart";
import { BarChart, Bar, XAxis, YAxis, PieChart, Pie, Cell, LineChart, Line, ResponsiveContainer } from "recharts";
import { TrendingUp, Users, Package, AlertCircle, DollarSign } from "lucide-react";

interface TopClient {
  name: string;
  sales: number;
  percentage: number;
}

interface TopProduct {
  name: string;
  units: number;
  revenue: number;
}

interface UnpaidItem {
  type: 'Sales' | 'Purchase Orders' | 'Deliveries';
  amount: number;
  count: number;
}

interface SalesData {
  month: string;
  sales: number;
}

export const SalesOverview = () => {
  // Mock data - in real app, this would come from your sales state/API
  const topClients: TopClient[] = [
    { name: "ABC Corporation", sales: 120000, percentage: 85 },
    { name: "XYZ Industries", sales: 95000, percentage: 70 },
    { name: "Tech Solutions Inc", sales: 78000, percentage: 60 },
    { name: "Global Enterprises", sales: 65000, percentage: 50 },
  ];

  const topProducts: TopProduct[] = [
    { name: "Laser Printer HP-2055", units: 320, revenue: 450000 },
    { name: "Ink Cartridge Canon", units: 180, revenue: 125000 },
    { name: "Toner Samsung MLT", units: 95, revenue: 85000 },
    { name: "Multifunction Brother", units: 45, revenue: 180000 },
  ];

  const unpaidItems: UnpaidItem[] = [
    { type: 'Sales', amount: 20000, count: 8 },
    { type: 'Purchase Orders', amount: 8000, count: 3 },
    { type: 'Deliveries', amount: 4000, count: 5 },
  ];

  const salesByProduct = [
    { name: "Printers", sales: 450000 },
    { name: "Cartridges", sales: 125000 },
    { name: "Toners", sales: 85000 },
    { name: "Parts", sales: 65000 },
    { name: "Services", sales: 35000 },
  ];

  const salesByClient = [
    { name: "ABC Corp", value: 120000, fill: "#0088FE" },
    { name: "XYZ Ind", value: 95000, fill: "#00C49F" },
    { name: "Tech Sol", value: 78000, fill: "#FFBB28" },
    { name: "Others", value: 145000, fill: "#FF8042" },
  ];

  const monthlySales: SalesData[] = [
    { month: "Jan", sales: 85000 },
    { month: "Feb", sales: 92000 },
    { month: "Mar", sales: 78000 },
    { month: "Apr", sales: 110000 },
    { month: "May", sales: 125000 },
    { month: "Jun", sales: 140000 },
  ];

  const unpaidByType = [
    { name: "Sales", value: 20000, fill: "#ef4444" },
    { name: "P.O.", value: 8000, fill: "#f97316" },
    { name: "Deliveries", value: 4000, fill: "#eab308" },
  ];

  const totalSales = useMemo(() => {
    return topClients.reduce((sum, client) => sum + client.sales, 0);
  }, [topClients]);

  const totalUnpaid = useMemo(() => {
    return unpaidItems.reduce((sum, item) => sum + item.amount, 0);
  }, [unpaidItems]);

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 to-blue-50">
      <div className="max-w-md mx-auto bg-white min-h-screen shadow-lg">
        <main className="flex-1 overflow-y-auto pb-20">
          <div className="px-4 py-6 space-y-4">
            {/* Summary Cards */}
            <div className="grid grid-cols-1 sm:grid-cols-3 gap-4">
              <Card className="rounded-xl shadow-md border-blue-200">
                <CardContent className="p-4 text-center">
                  <div className="text-2xl font-bold text-green-600">{totalSales.toLocaleString()}</div>
                  <div className="text-sm text-gray-500 mt-1">Total Sales</div>
                </CardContent>
              </Card>
              <Card className="rounded-xl shadow-md border-blue-200">
                <CardContent className="p-4 text-center">
                  <div className="text-2xl font-bold text-blue-600">{topProducts.reduce((sum, p) => sum + p.units, 0)}</div>
                  <div className="text-sm text-gray-500 mt-1">Units Sold</div>
                </CardContent>
              </Card>
              <Card className="rounded-xl shadow-md border-blue-200">
                <CardContent className="p-4 text-center">
                  <div className="text-2xl font-bold text-red-600">{totalUnpaid.toLocaleString()}</div>
                  <div className="text-sm text-gray-500 mt-1">Unpaid Amount</div>
                </CardContent>
              </Card>
            </div>

            {/* Top Clients */}
            <Card className="rounded-xl shadow-md border-blue-200">
              <CardHeader className="pb-3 bg-blue-50 rounded-t-xl">
                <CardTitle className="flex items-center gap-2 text-lg text-blue-800">
                  <Users className="h-5 w-5 text-blue-600" />
                  Top Clients
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-4 p-4">
                {topClients.map((client, index) => (
                  <div key={client.name} className="space-y-2">
                    <div className="flex justify-between items-center">
                      <span className="font-medium text-sm">{client.name}</span>
                      <span className="text-sm font-semibold">{client.sales.toLocaleString()}</span>
                    </div>
                    <Progress value={client.percentage} className="h-2" />
                  </div>
                ))}
              </CardContent>
            </Card>

            {/* Top Products */}
            <Card className="rounded-xl shadow-md border-blue-200">
              <CardHeader className="pb-3 bg-blue-50 rounded-t-xl">
                <CardTitle className="flex items-center gap-2 text-lg text-blue-800">
                  <Package className="h-5 w-5 text-green-600" />
                  Top Products
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-3 p-4">
                {topProducts.map((product, index) => (
                  <div key={product.name} className="flex justify-between items-center p-3 bg-gray-50 rounded-lg">
                    <div className="flex-1 min-w-0">
                      <div className="font-medium text-sm truncate">{product.name}</div>
                      <div className="text-xs text-gray-500">{product.units} units sold</div>
                    </div>
                    <div className="text-right">
                      <div className="font-semibold text-sm">{product.revenue.toLocaleString()}</div>
                    </div>
                  </div>
                ))}
              </CardContent>
            </Card>

            {/* Unpaid Summary */}
            <Card className="rounded-xl shadow-md border-red-200">
              <CardHeader className="pb-3 bg-red-50 rounded-t-xl">
                <CardTitle className="flex items-center gap-2 text-lg text-red-800">
                  <AlertCircle className="h-5 w-5 text-red-600" />
                  Unpaid Summary
                </CardTitle>
              </CardHeader>
              <CardContent className="space-y-3 p-4">
                {unpaidItems.map((item) => (
                  <div key={item.type} className="flex justify-between items-center p-3 bg-red-50 rounded-lg">
                    <div className="flex items-center gap-3">
                      <Badge variant="outline" className="text-red-700 border-red-300">
                        {item.count} items
                      </Badge>
                      <span className="font-medium text-sm">{item.type}</span>
                    </div>
                    <span className="font-semibold text-red-600">{item.amount.toLocaleString()}</span>
                  </div>
                ))}
              </CardContent>
            </Card>

            {/* Charts Section with proper spacing */}
            <div className="space-y-6">
              {/* Sales by Product Chart */}
              <Card className="rounded-xl shadow-md border-blue-200">
                <CardHeader className="pb-3 bg-blue-50 rounded-t-xl">
                  <CardTitle className="flex items-center gap-2 text-lg text-blue-800">
                    <TrendingUp className="h-5 w-5 text-purple-600" />
                    Sales by Product
                  </CardTitle>
                </CardHeader>
                <CardContent className="w-full p-4">
                  <div className="w-full h-60">
                    <ChartContainer config={{}} className="w-full h-full">
                      <ResponsiveContainer width="100%" height="100%">
                        <BarChart data={salesByProduct} margin={{ top: 20, right: 30, left: 20, bottom: 60 }}>
                          <XAxis 
                            dataKey="name" 
                            tick={{ fontSize: 12 }}
                            interval={0}
                            angle={-45}
                            textAnchor="end"
                            height={80}
                          />
                          <YAxis tick={{ fontSize: 12 }} />
                          <ChartTooltip content={<ChartTooltipContent />} />
                          <Bar 
                            dataKey="sales" 
                            fill="#8884d8" 
                            radius={[4, 4, 0, 0]}
                          />
                        </BarChart>
                      </ResponsiveContainer>
                    </ChartContainer>
                  </div>
                </CardContent>
              </Card>

              {/* Sales by Client Chart */}
              <Card className="rounded-xl shadow-md border-blue-200">
                <CardHeader className="pb-3 bg-blue-50 rounded-t-xl">
                  <CardTitle className="flex items-center gap-2 text-lg text-blue-800">
                    <Users className="h-5 w-5 text-blue-600" />
                    Sales by Client
                  </CardTitle>
                </CardHeader>
                <CardContent className="w-full p-4">
                  <div className="w-full h-60">
                    <ChartContainer config={{}} className="w-full h-full">
                      <ResponsiveContainer width="100%" height="100%">
                        <PieChart>
                          <Pie
                            data={salesByClient}
                            cx="50%"
                            cy="50%"
                            outerRadius={80}
                            dataKey="value"
                            label={({ name, percent }) => `${name} ${(percent * 100).toFixed(0)}%`}
                          >
                            {salesByClient.map((entry, index) => (
                              <Cell key={`cell-${index}`} fill={entry.fill} />
                            ))}
                          </Pie>
                          <ChartTooltip content={<ChartTooltipContent />} />
                        </PieChart>
                      </ResponsiveContainer>
                    </ChartContainer>
                  </div>
                </CardContent>
              </Card>

              {/* Monthly Sales Trend */}
              <Card className="rounded-xl shadow-md border-blue-200">
                <CardHeader className="pb-3 bg-blue-50 rounded-t-xl">
                  <CardTitle className="flex items-center gap-2 text-lg text-blue-800">
                    <TrendingUp className="h-5 w-5 text-green-600" />
                    Monthly Sales Trend
                  </CardTitle>
                </CardHeader>
                <CardContent className="w-full p-4">
                  <div className="w-full h-60">
                    <ChartContainer config={{}} className="w-full h-full">
                      <ResponsiveContainer width="100%" height="100%">
                        <LineChart data={monthlySales} margin={{ top: 20, right: 30, left: 20, bottom: 20 }}>
                          <XAxis dataKey="month" tick={{ fontSize: 12 }} />
                          <YAxis tick={{ fontSize: 12 }} />
                          <ChartTooltip content={<ChartTooltipContent />} />
                          <Line 
                            type="monotone" 
                            dataKey="sales" 
                            stroke="#10b981" 
                            strokeWidth={3}
                            dot={{ fill: '#10b981', strokeWidth: 2, r: 6 }}
                          />
                        </LineChart>
                      </ResponsiveContainer>
                    </ChartContainer>
                  </div>
                </CardContent>
              </Card>

              {/* Unpaid by Type Chart */}
              <Card className="rounded-xl shadow-md border-blue-200">
                <CardHeader className="pb-3 bg-blue-50 rounded-t-xl">
                  <CardTitle className="flex items-center gap-2 text-lg text-blue-800">
                    <DollarSign className="h-5 w-5 text-red-600" />
                    Unpaid by Type
                  </CardTitle>
                </CardHeader>
                <CardContent className="w-full p-4">
                  <div className="w-full h-60">
                    <ChartContainer config={{}} className="w-full h-full">
                      <ResponsiveContainer width="100%" height="100%">
                        <PieChart>
                          <Pie
                            data={unpaidByType}
                            cx="50%"
                            cy="50%"
                            innerRadius={40}
                            outerRadius={80}
                            dataKey="value"
                            label={({ name, value }) => `${name}: ${value.toLocaleString()}`}
                          >
                            {unpaidByType.map((entry, index) => (
                              <Cell key={`cell-${index}`} fill={entry.fill} />
                            ))}
                          </Pie>
                          <ChartTooltip content={<ChartTooltipContent />} />
                        </PieChart>
                      </ResponsiveContainer>
                    </ChartContainer>
                  </div>
                </CardContent>
              </Card>
            </div>
          </div>
        </main>
      </div>
    </div>
  );
};
